// google fonts api
@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@200;300;400;500;600;700;800;900&family=Manrope:wght@300;400;500;600;700&display=swap');
// reset default
#adminmenu .wp-menu-image img{padding: 7px 0 0;}
